.inProgress {
  border-bottom: 2px solid #1c87c3 !important;
  padding: 0px 0px 0px 10px;
}

.container {
  //border-bottom: 3px solid #eeeeee;
  display: flex !important;
  min-width: 150px;
  padding: 0px 0px 0px 10px;

  .barrier {
    border-left: 3px solid #f8f8f8;
    margin: 15px 0;
  }

  .button {
    padding: 10px;
    width: -moz-fit-content;
    width: fit-content;
    //min-width: 192.5px;

    .completedBox {
      background: #d9f5e4 !important;

      svg>g>path,
      svg>path {
        stroke: #1f9a50 !important;
      }
    }

    .inProgressBox {
      background: #cdecfe !important;

      svg>g>path,
      svg>path {
        stroke: #1c87c3 !important;
      }
    }

    .logoBox {
      padding: 7px 9px;
      background: #f6f6f6;
      border-radius: 10px;
      margin-right: 9px;
      width: 40px;
      height: 40px;

      svg>g>path,
      svg>path {
        stroke: #d2d3d4;
      }
    }

    .content {
      line-height: 12px;
      display: flex !important;
      justify-content: center;
      flex-direction: column;
      gap: 0.5em;
      height: 35px;

      .completedStatus {
        color: #1f9a50 !important;
      }

      .inProgressStatus {
        color: #1c87c3 !important;
      }

      .status {
        font-size: 10px;
        font-weight: 600;
        color: #d2d3d4;
      }

      .name {
        font-size: 13px;
        font-weight: 600;
        color: #090909;
        line-height: normal;
      }
    }
  }
}

.inventoryTab {
  display: flex;
  justify-content: start;
  align-items: center;
  overflow: auto;
  border-bottom: 2px solid #eeeeee;

  &::-webkit-scrollbar {
    border-radius: 100px;
    height: 6px;
  }

  &::-webkit-scrollbar-track {
    border-radius: 8px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 8px;
    -webkit-box-shadow: inset 0 0 6px rgba(112, 110, 110, 0.4);
  }
}